import { computed, reactive } from 'vue'
import { createResource } from 'frappe-ui'
import { users } from './users'
import router from '@/router'

export function sessionUser() {
  let cookies = new URLSearchParams(document.cookie.split('; ').join('&'))
  let _sessionUser = cookies.get('user_id')
  if (_sessionUser === 'Guest') {
    _sessionUser = null
  }
  return _sessionUser
}

export let session = reactive({
  login: createResource({
    url: 'opssalesportal.api.auth.login',
    makeParams({ email, password }) {
      return {
        usr: email,
        pwd: password,
      }
    },
    onSuccess(data) {
      const d = new Date();
      d.setTime(d.getTime() + (1*24*60*60*1000));
      let expires = "expires="+ d.toUTCString();
      document.cookie =`sid=${data.sid};${expires};path=/;`
      document.cookie =`user_id=${data.user_id};${expires};path=/;`
      document.cookie =`username=${data.username};${expires};path=/;`
      document.cookie =`email=${data.email};${expires};path=/;`
      users.reload()
      session.user = sessionUser()
      session.login.reset()
      router.replace(data.default_route || '/')
    },
  }),
  logout: createResource({
    url: 'opssalesportal.api.auth.logout',
    onSuccess() {
      users.reset()
      session.user = sessionUser()
      router.replace({ name: 'Login' })
    },
  }),
  user: sessionUser(),
  isLoggedIn: computed(() => !!session.user),
})
